
.login-input {
  display: block;
  width: 500px;
  height: 50px;
  padding: 0 10px;
  position: absolute;
  top: 40%;
  left: 50%;
  margin-left: -250px;
  font-size: 16px;
  border: none;
  color: #fff;
  font-size: 18px;
  font-weight: bold;
  border-bottom: 3px solid #ccc;
  background: transparent;
}
.login-input:focus {
    outline: none;
}



